.detailContents {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: calc(100% - 70px);
    right: -20px;
    z-index: -1;
    width: 192px;
    height: 184px;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      top: calc(100% - 70px);
      right: -25px;
      width: 241px;
      height: 231px;
    }

    @media screen and (min-width: 640px) {
      width: 289px;
      height: 277px;
      right: -35px;
    }

    @media screen and (min-width: 1152px) {

      &.bottom{
        top: calc(100% - 70px);
      }

      width: 364px;
      height: 401px;
      top: 0;
      right: -160px;
      transform: translateY(-40%);
    }

  }

  &-caption{
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    margin: 0px auto 60px;
    max-width: 260px;
    position: relative;
    display: flex;
    flex-direction: column;

    span.attention{
      position: relative;
    }

    span.attention::before{
      content: "";
      position: absolute;
      top: -17px;
      left: -17px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      width: 50px;
      height: 50px;
      background-image: url("data:image/svg+xml,%3Csvg width='50' height='51' viewBox='0 0 50 51' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M25 0.126953C38.8059 0.126953 50 11.3211 50 25.127C50 38.9328 38.8059 50.127 25 50.127C11.1941 50.127 0 38.9328 0 25.127C0 11.3211 11.1941 0.126953 25 0.126953Z' fill='%238C52FF' fill-opacity='0.2'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M25.0003 4.55664C36.3591 4.55664 45.5709 13.7684 45.5709 25.1272C45.5709 36.486 36.3591 45.6978 25.0003 45.6978C13.6415 45.6978 4.42969 36.486 4.42969 25.1272C4.42969 13.7684 13.6415 4.55664 25.0003 4.55664Z' fill='%238C52FF' fill-opacity='0.2'/%3E%3Cpath d='M24.9984 42.2273C34.4425 42.2273 42.0984 34.5714 42.0984 25.1273C42.0984 15.6833 34.4425 8.02734 24.9984 8.02734C15.5544 8.02734 7.89844 15.6833 7.89844 25.1273C7.89844 34.5714 15.5544 42.2273 24.9984 42.2273Z' fill='%238C52FF'/%3E%3C/svg%3E%0A");
      z-index: -1;
    }

    @media screen and (min-width: 480px) {
      max-width: 420px;
    }

    @media screen and (min-width: 640px){
      max-width: 560px;
      font-size: 20px;


      span.attention::before{
        width: 70px;
        height: 70px;
        top: -27px;
        left: -27px;
      }

    }
    @media screen and (min-width: 960px){
      max-width: 818px;
    }

    @media screen and (min-width: 1152px){
      font-size: 24px;
      max-width: 924px;

      span.attention::before{
        width: 100px;
        height: 100px;
        top: -37px;
        left: -37px;
      }


    }

  }

  &-block {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__title {
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      padding: 8px 28px;
      background: #8C52FF;
      border-radius: 10px 10px 0px 0px;
      max-width: 951px;
      min-width: 230px;
      width: calc(100% - 80px);
      text-align: center;

      @media screen and (min-width: 640px) {
        font-size: 16px;
      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;
      }


    }

    &__list {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 20px 10px 20px 15px;
      width: 100%;

      li {
        padding: 6px 0 6px 11px;
        position: relative;

        &.price-link a{
          color: #8C52FF;
          font-weight: 700;
        }


        @media screen and (min-width: 1152px) {
          padding-left: 16px;
        }

        &::before {
          content: "";
          width: 4px;
          height: 4px;
          background: #8C52FF;
          border-radius: 4px;
          position: absolute;
          top: 16px;
          left: 0;
          transform: translateY(-50%);

          @media screen and (min-width: 1152px) {
            width: 6px;
            height: 6px;
          }

        }
      }

      li a {
        font-size: 13px;
        line-height: 130%;
        letter-spacing: 0.05em;
        transition: all 0.25s ease;

        &:hover {
          border-bottom: 1px solid #262626;
        }

        @media screen and (min-width: 640px) {
          font-size: 16px;
        }

        @media screen and (min-width: 1152px) {
          font-size: 20px;
        }

      }

      @media screen and (min-width: 640px) {
        padding: 30px;
      }

      @media screen and (min-width: 830px) {
        columns: 2;
        column-gap: 40px;
      }

    }

    &__submenu{
      padding-top: 6px;
      li{
        padding-left: 27px !important;
        position: relative;
      }
      li::before{
        width: 17px !important;
        height: 3px !important;
        top: 16px;
        transform: translateY(-50%);
      }
    }

  }
}