.detailBanner {
  padding: 0px 0 45px;
  background: #fff;
  position: relative;

  & + .detailResult{
    padding-top: 40px;
  }

  & + .detailContents{
    padding-top: 40px;
  }


  .container{
    position: relative;
  }

  .section-h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;

    .violet{
        font-size: 24px;
      }

      @media screen and (min-width: 480px) {
        font-size: 40px;        
      }

      @media screen and (min-width: 640px) {
        font-size: 45px;        
      }

  }

  &-figure{
    display: none;

    @media screen and (min-width: 1440px) {
      display: block;
      position: absolute;
      bottom:-100px;
      left: -10px;
      transform: translateX(-100%);
      z-index: 1;
    }

  }

  @media screen and (min-width: 480px) {
    padding-bottom: 70px;

    .section-h1{
      .violet{
        font-size: 34px;
      }

    }

  }

  @media screen and (min-width: 640px) {
    padding-bottom: 87px;

    & + .detailResult{
      padding-top: 60px;
    }

    .section-h1{
      .violet{
        font-size: 40px;
      }

    }

  }

  @media screen and (min-width: 830px) {
    padding-bottom: 99px;
  }

  @media screen and (min-width: 960px) {

    .section-h1{
      max-width: 500px;
    }

  }

  @media screen and (min-width: 1152px) {
    padding-bottom: 90px;



    .section-h1{
      max-width: 680px;
      font-size: 53px;
      line-height: 110%;
      margin-bottom: 30px;

      .violet{
        font-size: 45px;
      }


    }

  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding-top: 54px / 320px * 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("data:image/svg+xml,%3Csvg width='320' height='54' viewBox='0 0 320 54' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M320 0C225.5 44.5 102.5 41.5 0 0V54H320V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");

    @media screen and (min-width: 480px) {
      padding-top: 70px / 480px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='480' height='70' viewBox='0 0 480 70' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M480 0C326.094 51.4943 159.224 50.6897 0 0V70H480V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 640px) {
      padding-top: 87px / 640px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='636' height='87' viewBox='0 0 636 87' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M636 0C430.792 64 208.299 63 -4 0V87H636V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 830px) {
      padding-top: 99px / 960px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='960' height='99' viewBox='0 0 960 99' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M960 0C652.188 72.8276 318.449 71.6897 0 0V99H960V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 1152px) {
      padding-top: 157px / 1920px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='1920' height='157' viewBox='0 0 1920 157' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1920 0C1285.2 121.29 645.13 127.72 0 0V157H1920V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

  }

  &-image {
    display: none;
  }

  .section-h1 {
    text-align: center;
    margin-bottom: 16px;
  }

  &-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 2;

    &__desc {
      text-align: center;
      font-size: 14px;
      line-height: 130%;
      letter-spacing: 0.02em;
      margin-bottom: 8px;

      @media screen and (min-width: 480px) {
        font-size: 16px;
        line-height: 130%;
      }

      @media screen and (min-width: 640px) {
        font-size: 18px;
        line-height: 150%;
        text-align: left;
      }

      @media screen and (min-width: 960px) {
        font-size: 22px;
        line-height: 150%;
      }

      @media screen and (min-width: 1152px) {
        font-size: 24px;
        line-height: 150%;
        margin-bottom: 20px;
      }

    }

    &__price {
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 20px;
      line-height: 140%;
      text-align: center;
      letter-spacing: 0.05em;
      color: #262626;
      position: relative;

      &::after{
        content: "";
        position: absolute;
        top: 50%;
        left: 9px;
        transform: translateY(-50%);
        width: 122px;
        height: 106px;
        z-index: -1;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='122' height='106' viewBox='0 0 122 106' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M49.4302 28.6467C34.0538 41.5188 2.74433 43.1459 0.20479 57.3921C-2.29997 71.4936 18.573 88.09 40.8375 97.4911C63.1368 106.928 86.7928 109.315 101.404 100.564C116.154 91.7058 121.859 71.7106 121.999 55.6927C122.103 39.5663 116.572 27.3811 108.535 17.3293C100.43 7.24127 89.8194 -0.677283 80.3918 0.0458719C71.0337 0.769027 60.7364 19.2456 49.4302 28.6467Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");

        @media screen and (min-width: 830px) {
          width: 151px;
          height: 132px;
          left: 0;
          top: 40%;          
        }

      }

      span.blue {
        font-size: 32px;
        line-height: 44px / 32px * 100%;
      }

      @media screen and (min-width: 1152px) {
        margin-bottom: 47px;
        font-size: 25px;

        span.blue{
          font-size: 40px;
        }

      }

    }

    &__btns {
      margin-bottom: 11px;

      .btn{
        padding: 14px;
        font-size: 13px;
        line-height: 17px;

        @media screen and (min-width: 480px) {
          padding: 15px;
        }

        @media screen and (min-width: 640px) {
          font-size: 16px;
          line-height: 21px;
          padding: 20px 17px;
        }

        @media screen and (min-width: 960px) {
          padding: 20px 27px;
        }

        @media screen and (min-width: 1152px) {
          font-size: 20px;
          line-height: 27px;
          padding: 23px 27px;
        }

      }

    }

    &__img {
      max-width: 330px;
      width: 100%;
      margin: 0 auto;

      & + .detailBanner-text__doctor{
        padding-top: 20px;
      }

      img {
        width: 100%;
        height: auto;
      }
    }

    &__features {
      margin-top: -29px;
      margin-bottom: -13px;
      width: 100%;
      overflow: visible;
      position: relative;
      z-index: 2;

      &::after {

        @media screen and (min-width: 640px) {
          display: none;
        }

        content: "";
        display: block;
        position: absolute;
        bottom: -50px;
        right: 0;
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("data:image/svg+xml,%3Csvg width='27' height='30' viewBox='0 0 27 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.8823 2.55056H17.2262C16.8741 2.55056 16.5882 2.8364 16.5882 3.18858C16.5882 3.54076 16.8741 3.82661 17.2262 3.82661H24.8823C25.2345 3.82661 25.5203 3.54076 25.5203 3.18858C25.5203 2.8364 25.2345 2.55056 24.8823 2.55056Z' fill='%23DDDDDD'/%3E%3Cpath d='M25.3345 2.73866L22.7824 0.186617C22.5336 -0.0622056 22.1291 -0.0622056 21.8802 0.186617C21.6314 0.435439 21.6314 0.839955 21.8802 1.08878L23.9806 3.18911L21.879 5.2907C21.6302 5.53953 21.6302 5.94404 21.879 6.19286C22.0041 6.31662 22.1673 6.37918 22.3307 6.37918C22.494 6.37918 22.6573 6.31668 22.7824 6.19286L25.3345 3.64082C25.5833 3.39194 25.5833 2.98748 25.3345 2.73866Z' fill='%23DDDDDD'/%3E%3Cpath d='M8.29562 2.55056H0.639553C0.287373 2.55056 0.00152649 2.8364 0.00152649 3.18858C0.00152649 3.54076 0.287373 3.82661 0.639553 3.82661H8.29562C8.6478 3.82661 8.93365 3.54076 8.93365 3.18858C8.93365 2.8364 8.6478 2.55056 8.29562 2.55056Z' fill='%23DDDDDD'/%3E%3Cpath d='M1.54043 3.18911L3.64076 1.08878C3.88958 0.839954 3.88958 0.435439 3.64076 0.186617C3.39194 -0.0622055 2.98742 -0.0622055 2.7386 0.186617L0.186617 2.73872C-0.0622056 2.98754 -0.0622056 3.39206 0.186617 3.64088L2.73866 6.19292C2.86373 6.31668 3.02702 6.37924 3.19037 6.37924C3.35372 6.37924 3.51701 6.31673 3.64082 6.19167C3.88964 5.94284 3.88964 5.53833 3.64082 5.28951L1.54043 3.18911Z' fill='%23DDDDDD'/%3E%3Cpath d='M24.2445 12.7596C23.7201 12.7596 23.2313 12.9192 22.8256 13.191C22.4759 12.1982 21.5278 11.4836 20.4165 11.4836C19.892 11.4836 19.4033 11.6432 18.9975 11.915C18.6479 10.9222 17.6998 10.2077 16.5884 10.2077C16.124 10.2077 15.6876 10.3327 15.3124 10.5509V6.37962C15.3124 4.97216 14.1679 3.82758 12.7604 3.82758C11.3529 3.82758 10.2083 4.97216 10.2083 6.37962V17.2257L7.99066 15.5631C6.67639 14.5767 4.80573 14.7082 3.64075 15.8719C2.89429 16.6184 2.89429 17.8318 3.64075 18.5783L12.7273 27.6661C13.8118 28.7507 15.255 29.3478 16.7888 29.3478H19.7785C23.6474 29.3478 26.7965 26.1999 26.7965 22.3298V15.3117C26.7965 13.9042 25.652 12.7596 24.2445 12.7596ZM25.5206 22.3297C25.5206 25.4955 22.9443 28.0718 19.7785 28.0718H16.7888C15.5945 28.0718 14.4728 27.6073 13.6294 26.7638L4.54291 17.6762C4.29409 17.4273 4.29409 17.0228 4.54291 16.774C4.93846 16.3797 5.46673 16.1768 5.99757 16.1768C6.42888 16.1768 6.8627 16.3108 7.22637 16.5838L10.4636 19.0121C10.6576 19.1575 10.9179 19.1793 11.1323 19.0721C11.3479 18.9636 11.4845 18.7429 11.4845 18.5017V6.37962C11.4845 5.67652 12.0561 5.10363 12.7605 5.10363C13.4648 5.10363 14.0364 5.67658 14.0364 6.37962V15.9497C14.0364 16.3019 14.3223 16.5877 14.6745 16.5877C15.0266 16.5877 15.3125 16.3019 15.3125 15.9497V12.7597C15.3125 12.0566 15.8841 11.4837 16.5885 11.4837C17.2928 11.4837 17.8645 12.0567 17.8645 12.7597V15.9497C17.8645 16.3019 18.1503 16.5877 18.5025 16.5877C18.8547 16.5877 19.1405 16.3019 19.1405 15.9497V14.0357C19.1405 13.3326 19.7122 12.7597 20.4165 12.7597C21.1209 12.7597 21.6925 13.3326 21.6925 14.0357V15.9497C21.6925 16.3019 21.9784 16.5877 22.3305 16.5877C22.6827 16.5877 22.9686 16.3019 22.9686 15.9497V15.3117C22.9686 14.6086 23.5402 14.0357 24.2446 14.0357C24.9489 14.0357 25.5206 14.6086 25.5206 15.3117V22.3297Z' fill='%23DDDDDD'/%3E%3C/svg%3E%0A");
      }

      .swiper-wrapper {
        overflow: visible;
      }

    }

    &__feature {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      position: relative;
      padding: 16px 71px 13px 15px;
      height: auto;
      display: flex;
      align-items: center;

      .icon {
        position: absolute;
        top: -10px;
        right: 5px;
        width: 50px;
        height: 50px;

        svg {
          width: 100%;
          height: 100%;
        }
      }

      .text {
        font-size: 13px;
        line-height: 130%;
      }

    }

    &__doctor {
      display: flex;
      align-items: center;
      width: 100%;
      padding-top: 50px;
      margin-bottom: 20px;

      .image {
        width: 80px;
        height: 80px;
        flex-shrink: 0;
        margin-right: 15px;
      }

      .caption {
        &-name {
          margin-bottom: 5px;
        }

        &-name, &-info {
          font-size: 10px;
          line-height: 150%;
          letter-spacing: 0.05em;
        }
      }

      @media screen and (min-width: 640px) {
        max-width: 100% !important;
        .image {
          width: 90px;
          height: 90px;

          img {
            width: 100%;
            height: auto;
          }
        }
        .caption {
          &-name {
            font-size: 16px;
          }

          &-info {
            font-size: 13px;
          }
        }
      }

      @media screen and (min-width: 1152px) {
        .image {
          width: 120px;
          height: 120px;
          margin-right: 36px;
        }
        .caption {
          &-name {
            font-size: 20px;
          }

          &-info {
            font-size: 16px;
          }
        }
      }

    }
  }

  @media screen and (min-width: 640px) {
    .container {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      position: relative;
    }

    .section-h1 {
      text-align: left;
    }

    &-text {
      width: 100%;
      max-width: 620px;
      align-items: flex-start;

      & > * {
        max-width: 60%;
      }

      &__img {
        display: none;
      }

      &__features {
        margin-top: 95px;
        max-width: 100%;

      }

      &__feature {
        padding: 23px 23px 23px 15px;

        .icon {
          width: 60px;
          height: 60px;
          top: -30px;
        }

        .text {
          font-size: 16px;
          line-height: 140%;
        }
      }

    }

    &-image {
      display: block;
      position: absolute;
      top: 0;
      right: -114px;
      bottom: 0;
      max-width: 449px;
      width: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

  }

  @media screen and (min-width: 830px) {

    &-image {
      max-width: 609px;
      right: -74px;
    }

    &-text {
      &__features {
        margin-top: 63px;
      }
    }

  }

  @media screen and (min-width: 960px){
    &-image img{
      object-position: top;
    }
  }

  @media screen and (min-width: 1152px) {

    &-image {
      max-width: 700px;
    }

    &-text {
      max-width: 850px;

      &__feature {
        padding: 18px 28px 18px 75px;
        max-width: 280px;

        .icon {
          width: 85px;
          height: 85px;
          right: auto;
          left: -20px;
        }

        .text{
          font-size: 20px;
          line-height: 140%;
        }

      }
    }

  }

}